:root {
  --accent: #27E7DB;
  --plasma: "Plasma Drip";
  --voronov: "VoronovFont";
}

@font-face {
  font-family: "Plasma Drip";
  src: url("../fonts/plasma/plasma.eot");
  src: local("plasma"), url("../fonts/plasma/plasma.eot?#iefix") format("embedded-opentype"), url("../fonts/plasma/plasma.woff2") format("woff2"), url("../fonts/plasma/plasma.woff") format("woff"), url("../fonts/plasma/plasma.ttf") format("truetype"), url("../fonts/plasma/plasma.otf") format("opentype"), url("../fonts/plasma/plasma.svg") format("svg");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "VoronovFont";
  src: url("../fonts/voronov/Ubuntu-Regular.eot");
  src: local("Ubuntu-Regular"), url("../fonts/voronov/voronov.eot?#iefix") format("embedded-opentype"), url("../fonts/voronov/voronov.woff2") format("woff2"), url("../fonts/voronov/voronov.woff") format("woff"), url("../fonts/voronov/voronov.ttf") format("truetype"), url("../fonts/voronov/voronov.otf") format("opentype"), url("../fonts/voronov/voronov.svg") format("svg");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
.svg-svg-burger {
  background: url("../images/sprites/main.svg") no-repeat;
}

.svg-svg-burger {
  background-position: 0 0;
  background-size: 100% auto;
  width: 52px;
}

.svg-svg-burger:before {
  content: "";
  display: block;
  font-size: 0;
  line-height: 0;
  padding-top: 100%;
}

.svg-svg-burger-dims {
  width: 52px;
  height: 52px;
}

.image-by-height {
  width: auto;
}

.image-by-height:before {
  content: none;
}

.image-by-height > svg {
  height: inherit;
}

.image-by-height > img {
  height: inherit !important;
  vertical-align: top !important;
  margin: 0 !important;
  padding: 0 !important;
  max-width: none !important;
  max-height: none !important;
  border: 0 !important;
  opacity: 0 !important;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.content-box-component {
  box-sizing: content-box;
}

body {
  display: flex;
  flex-direction: column;
  background: #000A24;
  color: #fff;
  font-family: "Montserrat", "Helvetica", Arial, sans-serif;
  width: 100%;
  min-width: 320px;
  height: 100%;
  min-height: 100vh;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
body.no-scroll {
  overflow: hidden;
}

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  padding: 0;
  cursor: pointer;
  background: transparent;
  border: none;
  color: inherit;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

p {
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

svg {
  max-width: 100%;
  max-height: 100%;
}

picture {
  display: block;
}

.background-image {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: -1;
  border-radius: 10px;
  overflow: hidden;
}
.background-image picture {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.background-image picture img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
  border-radius: 10px;
}

.container {
  width: 100%;
  max-width: 1300px;
  margin: 0 auto;
}

@media screen and (max-width: 1440px) {
  .container {
    padding: 0 15px;
  }
}
.big-title {
  font-weight: normal;
  font-size: 130px;
  line-height: 100%;
  font-family: var(--plasma);
  text-align: center;
}

.text {
  width: 100%;
  max-width: 702px;
  margin: 0 auto;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  text-align: center;
}

.swiper-button-prev {
  top: 143px;
