  align-items: center;
}
.hq__title-main {
  position: relative;
  z-index: 10;
  white-space: nowrap;
}
.hq__title-fon {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  filter: blur(15px);
  white-space: nowrap;
}
.hq__title-fon .hq__title-ms {
  color: #70C53A;
}
.hq__title-ms {
  color: var(--accent);
}
.hq__text {
  width: 100%;
  max-width: 702px;
  margin: 0 auto;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  text-align: center;
}
.hq__swiper {
  padding-top: 55px;
  position: relative;
}
.hq__swiper-wrapper {
  overflow-x: hidden;
}
.hq__list {
  width: 100%;
  max-width: 1238px;
  margin: 0 auto;
  padding-left: 3px;
  position: relative;
}
.hq__item {
  width: 257px;
  position: relative;
}
.hq__item:nth-child(4n) {
  margin-right: 0;
}
.hq__item::after {
  content: "";
  position: absolute;
  right: -63px;
  top: 60px;
  height: 131px;
  width: 63px;
  background: url("../images/blocks/hq-item.svg");
  background-repeat: no-repeat;
  background-size: contain;
}
.hq__item-photo {
  width: 100%;
  height: 194px;
  overflow: hidden;
  position: relative;
  z-index: 10;
}
.hq__item-photo img {
  width: 100%;
  height: auto;
}
.hq__item-img {
  padding: 3px;
  width: 100%;
  position: relative;
}
.hq__item-border {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  border: 3px solid var(--accent);
}
.hq__item-border::after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  border: 5px solid #81E03D;
  filter: blur(20px);
}
.hq__item-info {
  padding-top: 28px;
}
.hq__item-name {
  margin-bottom: 8px;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
}
.hq__item-city {
  font-weight: 500;
  font-size: 12px;
  line-height: 15px;
}

@media screen and (max-width: 1700px) {
  .hq__donut {
    left: calc(100% - 1400px);
  }
}
@media screen and (max-width: 1500px) {
  .hq .swiper-button-prev {
    left: -50px;
  }
  .hq .swiper-button-next {
    right: -70px;
  }
}
@media screen and (max-width: 1439px) {
  .hq__inner {
    padding: 0 50px;
  }
  .hq__item-img {
    height: auto;
  }
  .hq__item-photo {
    height: auto;
  }
  .hq__item::after {
    top: calc(100% - 202px);
  }
  .hq__swiper-wrapper {
    width: calc(100% - 100px);
    margin: 0 auto;
  }
  .hq .swiper-button-prev {
    top: calc(50% + 55px - 71px);
    left: 0;
  }
  .hq .swiper-button-next {
    top: calc(50% + 55px - 71px);
    right: -20px;
  }
}
@media screen and (max-width: 1365px) {
  .hq__header {
    padding-top: 250px;
  }
  .hq__donut {
    width: 300px;
    height: auto;
    top: 0;
    left: 0;
  }
}
@media screen and (max-width: 767px) {
  .hq__text {
    font-size: 14px;
    line-height: 21px;
  }
}
@media screen and (max-width: 639px) {
  .hq__donut {
    width: 240px;
    left: -50px;
  }
  .hq__header {
    padding-top: 195px;
  }
  .hq__inner {
    padding: 0;
  }
  .hq__title {
    font-size: 60px;
    line-height: 84px;
  }
  .hq__item {
    width: 208px;
  }
  .hq__item-info {
    padding-top: 20px;
  }
  .hq__item::after {
    width: 51px;
    height: 105px;
    right: -50px;
    top: 50px;
    background-image: url("../images/blocks/hq-item-mob.svg");
  }
  .hq__swiper-wrapper {
    width: calc(100% - 60px);
    margin-left: 60px;
  }
  .hq .swiper-button-prev {
    display: none;
  }
  .hq .swiper-button-next {
    right: auto;
    left: 16px;
  }
}
.download__form {
  width: 100%;
  max-width: 1280px;
  margin: 0 auto;
  display: flex;
  align-items: flex-start;
}
.download__form-field {
  margin-right: 64px;
  width: 479px;
  height: 236px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 2px dashed var(--accent);
  border-radius: 18px;
  cursor: pointer;
}
.download__form-field span {
  text-decoration: underline;
  text-align: center;
  color: var(--accent);
  font-size: 18px;
  line-height: 22px;
}
.download__form-field input {
  display: none;
}
.download__form-result {
  width: 414px;
}
.download__form-title {
  margin-bottom: 42px;
  font-weight: 400;
  font-size: 32px;
  line-height: 39px;
  color: #27E7DB;
}
.download__form-inputs input {
  display: block;
  width: 100%;
  height: 45px;
  font-size: 14px;
  line-height: 17px;
  background: transparent;
  border: none;
  border-bottom: 1px solid #fff;
  color: #fff;
}
.download__form-inputs input::placeholder {
  font-size: 14px;
  color: #fff;
}
.download__form-btn {
  margin-left: 40px;
  min-width: 252px;
  height: 289px;
  padding-bottom: 118px;
  background: url("../images/blocks/download-btn.png"), url("../images/blocks/download-btn-hover.png");
  background-size: cover;
  background-repeat: no-repeat;
  color: #000;
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  text-align: center;
}
.download__form-btn:hover, .download__form-btn:focus, .download__form-btn:active {
  background-image: url("../images/blocks/download-btn-hover.png");
}

@media screen and (max-width: 1439px) {
  .download__form-field {
    margin-right: 60px;
  }
  .download__form-result {
    flex-grow: 1;
  }
}
@media screen and (max-width: 1200px) {
  .download__inner {
    padding: 0 16px;
  }
  .download__form {
    width: 100%;
    max-width: 800px;
    flex-direction: column;
    align-items: center;
  }
  .download__form-result {
    width: 100%;
    margin-bottom: 40px;
    order: -1;
  }
  .download__form-field {
    margin: 0;
    margin-bottom: 40px;
    width: 100%;
  }
  .download__form-btn {
    margin-left: 0;
  }
}
@media screen and (max-width: 767px) {
  .download__form-title {
    margin-bottom: 10px;
    font-size: 24px;
    line-height: 29px;
  }
  .download__form-result {
    margin-bottom: 33px;
  }
  .download__form-field {
    height: 200px;
  }
  .download__form-field span {
    font-size: 18px;
    line-height: 22px;
  }
  .download__form-field span br {
    display: none;
  }
  .download__form-btn {
    padding-bottom: 0;
    width: 190px;
    min-width: 190px;
    height: 230px;
    background-size: contain;
    position: relative;
  }
  .download__form-btn span {
    position: relative;
    top: -50px;
  }
}
.park {
  position: relative;
  background: url("../images/blocks/stars2.svg");
}
.park::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background: #fff;
  height: 130px;
}
.park__img {
  position: relative;
  z-index: 10;
}
.park__img img {
  width: 100%;
}

